import * as React from 'react';
import { MenuItem } from '@ali/we-design';
import { PluginRoles } from '@ali/4ever-bamboo';
import { MenuItemInnerProps } from '@ali/4ever-component';
import { adjustSz } from '../actions';

export function SzBiggerMenu(props: MenuItemInnerProps) {
  const { menuItem, controller } = props;
  const { title, ...rest } = menuItem;

  const handleClick = () => {
    controller.run('onAction', adjustSz(true));
  };

  return (
    <MenuItem {...rest} onClick={handleClick} role={PluginRoles.szIncrease}>
      {title}
    </MenuItem>
  );
}

export function SzSmallerMenu(props: MenuItemInnerProps) {
  const { menuItem, controller } = props;
  const { title, ...rest } = menuItem;

  const handleClick = () => {
    controller.run('onAction', adjustSz(false));
  };

  return (
    <MenuItem {...rest} onClick={handleClick} role={PluginRoles.szDecrease}>
      {title}
    </MenuItem>
  );
}

